*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
    background-color: rgba(161, 208, 238, 0.3);
}
nav a{
    text-decoration: none;
    color: rgb(24, 80, 201);
    font-size: 2em;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}
nav div form{
    display: flex;
    flex-direction: row;
    gap: 8px;
    
}
nav div input{
    padding: 10px;
    border-radius: 5px;
    font-size: 1em;
    border: 2px lightslategray solid;
    outline: none;
}
nav div input:focus{
    border: 1px rgb(24, 80, 201) solid;
    box-shadow: 0px 0px 2px rgb(24, 80, 201);
}
nav div button{
    padding: 10px;
    border-radius: 5px;
    background-color: transparent;
    font-size: 1em;
    font-weight: bold;
    color: rgb(24, 80, 201);
    border: 2px rgb(24, 80, 201) solid;
    cursor: pointer;
}
nav div button:hover{
    background-color: rgb(24, 80, 201);
    color: white;
    box-shadow: 0px 0px 3px rgb(24, 80, 201);
}

.add{
    background-color: black;
    color: white;
    text-align: center;
    border: none;
    font-size: 1em;
    border-radius: 5px;
    float: right;
    padding: 10px 15px;
    margin: 20px 100px;
    cursor: pointer;
}
.add:hover{
    box-shadow: 0px 0px 3px black;
    font-weight: bold;
}
.list{
    margin: 70px 100px;
}
.post{
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    gap: 5px;
}
.post .link{
    font-size: 1.5em;
    color: rgb(24, 80, 201);
}
.post .content{
    font-size: 1.2em;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.singlelink{
    font-size: 2.5em;
}
.singlecontent{
    font-size: 1.2em;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.back{
    text-decoration: none;
}

.container{ 
    margin: 100px 100px 0px 100px;
}
.container form{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.container input{
    padding: 10px;
    border-radius: 5px;
    font-size: 1em;
    border: 2px lightslategray solid;
    outline: none;
}
.container input:focus{
    border: 1px rgb(24, 80, 201) solid;
    box-shadow: 0px 0px 2px rgb(24, 80, 201);
}
.container textarea{
    padding: 10px;
    border-radius: 5px;
    font-size: 1em;
    border: 2px lightslategray solid;
    outline: none; 
}
.container textarea:focus{
    border: 1px rgb(24, 80, 201) solid;
    box-shadow: 0px 0px 2px rgb(24, 80, 201);
}
.container button{
    background-color: rgb(24, 80, 201);
    color: white;
    text-align: center;
    border: none;
    font-size: 1em;
    border-radius: 5px;
    align-self: flex-end;
    padding: 10px 15px;
    margin: 20px 100px;
    cursor: pointer;
    width: fit-content;
    font-weight: bold;
    border: 2px rgb(24, 80, 201) solid;
    cursor: pointer;
}
.container button:hover{
    background-color: rgb(24, 80, 201);
    color: white;
    box-shadow: 0px 0px 3px rgb(24, 80, 201);
}
.container button:disabled{
    opacity: 0.5;
}